import React, { useState } from 'react';
import serviceAPI from './API/serviceAPI';
function Services() {
    const [state, setState] = useState(serviceAPI);
    console.log(state, 'Servise Data');
    return (
        <>
            <section className="services-main-container">
                <div className="container service-container">
                    <h1 className="main-heading text-center fw-bold">Our Services</h1>
                    <div className="row">
                        {state.map((currentElm) => {
                            const { id, logo, title, info } = currentElm;
                            return (
                                <>
                                    <div className="col-11 col-lg-4 col-xxl-4 work-container-subdiv" key={id}>
                                        <i className={`fontawesome-style ${logo}`}></i>
                                        <h2 className="sub-heading">{title}</h2>
                                        <p className="main_para">{info}</p>
                                    </div>
                                </>
                            );
                        })}
                    </div>
                </div>
            </section>
        </>
    );
}

export default Services;
